import React, { Component } from 'react'
import moment from 'moment'
import uuid from 'uuid'
import {Link} from 'react-router-dom'
export class Post extends Component {


    shrinkText = (text) => {
      let str = text
      function WordCount(string) { 
        return string.split(" ").length;
    }
    let mill =str.slice(str.indexOf('.')+1 ,str.length)
    let re=mill
    let count = WordCount(str.slice(0,str.indexOf('.')+1 ))

    while (count<50) {
      mill=str.slice(count,str.indexOf('.')+1 )
      re+=mill
      count+=WordCount(mill)

    }
    return re
    }
  render() {
      let o= this.props.post
    return (
      
      <div className="card ">
      <small style={{position:'absolute',top:'0',right:'0'}} className="helpText">{moment(moment(o.created).format("YYYYMMDD"), "YYYYMMDD").fromNow()}</small>
      <div className="contain">
       <Link onClick={()=> this.props.postPage(o.slug)} to={"posts/"+ o.slug} ><img src={o.image} className="card-img" alt={o.title} /></Link> 
        <div className="card-bod">
          <h4 className="card-title">{o.title}</h4>
          <Link onClick={()=> this.props.postPage(o.slug)} to={"posts/"+ o.slug} style={{color:"inherit"}}> <p className="card-text">{o.body.slice(0, 200) + '...'}</p></Link>
        </div>
      </div>
      <div className="tags">{o.categories.map(u=>(<span className="badge" key={uuid()}>{u}</span>))}</div>
      

    </div>
    )
  }
}

export default Post
