import React, { Component } from 'react'

export class CheckBox extends Component {


  state = {
    checked: false
  }
    onClick = (e) => {
      //  document.querySelector('div #'+e.target.value).className="blue"
        let {checked} =this.state
        this.setState({checked:!checked})
        this.props.throwName(this.props.conder.name, e.target.checked)
        //e.target.checked?addedTags.push(e.target.value):
        
      }
  render() {
      let {conder:i} =this.props
    return (
      <div>
          <div id={i.name} className={this.props.checked?"blue":"div"}>
                       <input name={i.name} checked={this.props.checked} id={"#"+ i.name} className="form-check-in" type="checkbox"  onChange={this.onClick}/>
                    <label  className="form-check-label" htmlFor="defaultCheck1">
                      <i style={{margin:"0 10px"}} className={i.tag} aria-hidden="true" ></i>
                      {i.name}
                     </label><br/>
                     </div>
      </div>
    )
  }
}

export default CheckBox
