import React, { Component } from 'react'
import Axios from 'axios';
import {Post} from '../Post/Post';

import uuid from 'uuid'
export class PostContainer extends Component {
    state = {
        posts: [],
        pagenate: 5,
        colors:['#2185C5', '#7ECEFD',  '#001f3f','#0074D9'
        , '#7FDBFF'
        , '#39CCCC'
        , '#3D9970',
         '#2ECC40',
         '#01FF70',
         '#FFDC00',
         '#FF851B',
         '#FF4136',
         '#85144b',
         '#F012BE'
         
        ]
    }

    componentDidMount() {
     
        Axios.get('posts.json', {method: 'GET'}).then(v=>
          this.setState({posts: v.data})
        ).catch(console.log)
      }
      
      randomColor= ()=>{
        let {colors} = this.state
        return colors[Math.floor(Math.random() * colors.length)]
      }
   
  render() {
    let {posts}= this.state
    return (
      <div>
     
    
    
    {posts.map(item => (
      <React.Fragment key={uuid()}>
     
    <Post post={item} key={uuid()} postPage={this.props.compFun} color={this.randomColor()} />
    <hr style={{width:'80%',margin:'0 auto'}}/>
    </React.Fragment>))}
        
  
     
      </div>
    )
  }
}

export default PostContainer
