import React, { Component } from 'react'
import { Link} from 'react-router-dom'
import './CreatePost.css'
import logo from '../logo1.svg'
import uuid from 'uuid'
import CheckBox from './CheckBox';


export class CreatePost extends Component {
  state ={
    dev:false,
    image:'',
    body:'',
    title:'',
    addedTags:new Map(),
    tags:[ {name:'Trivial', tag: 'fa fa-bicycle'}, {name:'Story', tag:'fa fa-file-archive-o'}, {name:'Sports',tag:'fa fa-star'}, {name:'Other', tag:'fa fa-fighter-jet'}]
  }
  /**
   *
   *
   * @param {DragEvent} e
   */
  addTag=(item, isChecked) => {
   
    this.setState(prevState => ({ addedTags: prevState.addedTags.set(item, isChecked) }));


  }
  onChange=(e)=>{
    this.setState({[e.target.id]:e.target.value})
  }
  fileUp = (e) => {
    const files = Array.from(e.target.files)
    console.log(typeof files)
    this.setState({ image: files })
  }
  onSubmit =(e) => {
    e.preventDefault()
    let comp = true
    typeof this.state.image!=="string"&& this.state.title.length >0&&this.state.body.length>0? console.log("iimage is there"): comp= false;
    const files = this.state.image,title=this.state.title,body=this.state.body, addedTags=this.state.addedTags
    const formData = new FormData()
    formData.append("i", "file")
    console.log(formData)
    //>0? console.log("boiedd"):comp false;
    if (comp===true) sub()
    
    else {alert("error incomplete post")
  e.preventDefault()}
    function sub (){
      let arr = []
  
    files.forEach((file, i) => {
      formData.append('file', file)
    })
    for (const k of addedTags.keys()) {
      arr.push(k)
    }
    console.log(formData.getAll('file'))
    formData.append("title", title)
    formData.append("tags", JSON.stringify(arr))
    formData.append("body", body)
    fetch('http://localhost/newPost',
    {method:'POST',
    headers:new Headers({
      'Accept': '*',
      'Content-Type':'multipart/form-data',
      
      }),
    body: FormData})
    .then(i=> i.text().then(console.log))
    .catch(console.log)
    //alert("yayy")
    }
  }
  render() {
    let {tags}= this.state
    return (
      <div>
          <header className="header" >
              <Link to="/"> <div className= 'logo'><img style={{width:'100px'}} alt="logo" src={logo}/>
                <h2 id="siteHead">ColourfulLanguage</h2></div>
                </Link> 

                <nav className="nav">
                    <li className="nav-item">
                    <Link to="/" className="nav-link" >Home</Link>
                    </li>
                    <li className="nav-item">
                    
                    <a href="/about.html" className="nav-link" >About</a>
                    </li>
                    <li className="nav-item">
                    <a href="/contact.html" className="nav-link" >Contact</a>
                    </li>
                    <li className="nav-item">
                    <a href="https://kosyalli.com/game?game=all&amp;ref=colourfulLang" className="nav-link" >Game</a>
                    </li>
                </nav>
            </header>
        <div className="con">
          <div className="container" id="editerId"style={{height:"100%"}}>
           <div className="quill">
              <form action={this.state.dev? "http://localhost/api/newPost":'/api/newPost'}  encType="multipart/form-data" method="post">
                  <div className="form-group">
                    <label htmlFor="exampleFormControlInput1">Title</label>
                    <input name="title" onChange={this.onChange} type="text" className="form-control" id="title" placeholder="Your Title"/>
                  </div>
                  <div className="form-check form-group">
                   {
                     tags.map(i=>( 
                      
                       <CheckBox key={uuid()} conder={i} throwName={this.addTag} checked={this.state.addedTags.get(i.name)}/>
                    ))}
                  </div>
                  <div className="form-group">
                  <label htmlFor="exampleFormControlInput1">Image</label>
                      <div className="file-upload-wrapper">
                        <input  name="file" onChange={this.fileUp} type="file" id="input-file-now-custom-1" className="file-upload" data-default-file="https://mdbootstrap.com/img/Photos/Others/images/89.jpg" />
                      </div>
                  </div>
                  <div className="form-group">
                    <label htmlFor="body">Example textarea</label>
                    <textarea name="body" onChange={this.onChange} className="form-control" id="body" rows="8"></textarea>
                  </div>
                  <button type="submit">Submit</button>
                </form>
           </div>
          </div>
        </div>
      </div>
    )
  }
}


export default CreatePost
