import React, {
    Component
} from 'react'

export class CanvasComponent extends Component {
    componentDidMount() {
        const utils = {
            randomIntFromRange: function (min, max) {
                return Math.floor(Math.random() * (max - min + 1) + min)
            },

            randomColor: function (colors) {
                return colors[Math.floor(Math.random() * colors.length)]
            },

            distance: function (x1, y1, x2, y2) {
                const xDist = x2 - x1
                const yDist = y2 - y1

                return Math.sqrt(Math.pow(xDist, 2) + Math.pow(yDist, 2))
            }
        }

        document.querySelector('#p') !== null ? bacg() : console.log('Switch to browser for amazing background')

        function bacg() {
            const canvas = document.querySelector('canvas')
            const c = canvas.getContext('2d')

            canvas.width = canvas.parentElement.clientWidth //innerWidth
            canvas.height = canvas.parentElement.clientHeight



            // const colors = ['#2185C5', '#7ECEFD', '#001f3f', '#0074D9', '#7FDBFF', '#39CCCC', '#3D9970',
            //     '#2ECC40',
            //     '#01FF70',
            //     '#FFDC00',
            //     '#FF851B',
            //     '#FF4136',
            //     '#85144b',
            //     '#F012BE'

            // ]

            // Event Listeners



            window.addEventListener('resize', () => {
                canvas.width = canvas.parentElement.clientWidth //innerWidth
                canvas.height = canvas.parentElement.clientHeight
                init()
            })



            // Circles
            class Circle {
                constructor(x, y, radius, color) {
                    this.x = x
                    this.y = y
                    this.radius = radius;
                    this.velocity = {
                        x: utils.randomIntFromRange(-14, 14),
                        y: 10
                    }
                    this.friction = 0.8
                    this.color = color
                }
            }

            
          
            Circle.prototype.draw = function () {
                c.save()
                c.beginPath()
                c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false)
                c.fillStyle = this.color
                if(this.radius > 3 ) {
                    this.color = m1.update() }
                c.fill()
                c.closePath()
                c.restore()
            }

            Circle.prototype.update = function () {

                this.draw()
                if (this.y + this.radius + this.velocity.y  > canvas.height-groundHeight) {
                    this.velocity.y = -this.velocity.y * this.friction;
                    this.shatter()
                } else {
                    this.velocity.y += this.radius * 0.05
                }
               if (this.x + this.radius + this.velocity.x > canvas.width || this.x - this.radius <= 0 ){this.velocity.x = -this.velocity.x
                this.shatter()} 
                this.x += this.velocity.x
                this.y += this.velocity.y

            }

            class Mini extends Circle {
                constructor(x, y, radius, r, g, b) {
                    super(x, y, radius)
                    this.r = r;
                    this.g = g;
                    this.b = b
                    this.color = `rgba(${r},${g},${b}, 1)`
                    this.velocity = {
                        x: utils.randomIntFromRange(-5, 5),
                        y: utils.randomIntFromRange(-15, 15)
                    }
                    this.friction = 0.8;
                    this.ttl = 200;
                    this.opacity = 1
                }
            }

            Mini.prototype.draw = function () {

                c.save()
                c.beginPath()

                c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false)
                c.fillStyle = this.color
                c.shadowColor = this.color
                c.shadowBlur = 5
                c.fill()
                c.closePath()
                c.restore()
                this.color = `rgba(${this.r},${this.g},${this.b}, ${this.opacity})`

            }
            Mini.prototype.update = function () {

                this.draw()
                if (this.y + this.radius + this.velocity.y  > canvas.height-groundHeight) {
                    this.velocity.y = -this.velocity.y * this.friction
                } else {
                    this.velocity.y += .1
                }
                this.y += this.velocity.y
                this.x += this.velocity.x
                this.ttl -= 1;
                this.opacity -= 1 / this.ttl
            }


            function createMountainRange(am, h, co) {
                for (let i = 0; i < am; i++) {
                    const mw = canvas.width / am
                    c.beginPath()
                    c.moveTo(i * mw, canvas.height)
                    c.lineTo(i * mw + mw + 325, canvas.height)
                    c.lineTo(i * mw + mw / 2, canvas.height - h)
                    c.lineTo(i * mw - 325, canvas.height)
                    c.fillStyle = co;
                    c.fill();
                    c.closePath()
                }
            }
            /**
             *
             *
             * @param {number} r
             * @param {number} g
             * @param {number} b
             */
            function col(r, g, b) {
                this.r = r; this.g = g; this.b = b
                this.red = 0.05;
                this.green = 0.05;
                this.blue = 0.05
            }
            col.prototype.update = function () {
                if(this.r > 254 || this.r < 1){ this.red = -this.red }
                if(this.g > 254 || this.g < 1 ) this.green = -this.green
                if(this.b > 254 || this.b < 1)  this.blue = -this.blue
                this.r += this.red;
                this.blue += this.blue;
                this.g += this.green
                return 'rgb(' + this.r + ',' + this.g + ',' + this.b + ')'
            }
            class Star extends Circle {
                constructor(x, y, radius, color) {
                    super(x, y, radius)
                    this.color = color
                }
            }

            Circle.prototype.shatter = function () {
                this.radius -= 3
                for (let i = 0; i < Math.abs(this.radius); i++) a.push(new Mini(this.x, this.y, Math.max(this.radius / 5, 1), utils.randomIntFromRange(0, 255), utils.randomIntFromRange(0, 255), utils.randomIntFromRange(0, 255)))
            }
            const m1 = new col(utils.randomIntFromRange(0, 255), utils.randomIntFromRange(0, 255), utils.randomIntFromRange(0, 255))


            // Implementation

            const gradient = c.createLinearGradient(0, 0, 0, canvas.width)
            gradient.addColorStop(1, '#3f586b')
            let circles, a = [],groundHeight=100,
                b, ticker = 0

            function init() {

                circles = [];
                b = []
                for (let i = 0; i < 1; i++) {


                    circles.push(new Circle(canvas.width / 2, 0, 20, "white"))
                }
                for (let i = 0; i < 150; i++) b.push(new Star(Math.random() * canvas.width, Math.random() * canvas.height, utils.randomIntFromRange(0, 3), 'white'))



            }

            // Animation Loop
            function animate() {

                requestAnimationFrame(animate)
                c.fillStyle = gradient
                c.fillRect(0, 0, canvas.width, canvas.height)
                b.forEach(i => i.draw())
                createMountainRange(1, canvas.height - 50, '#384551')

                createMountainRange(2, canvas.height - 100, '#2b3843')
                createMountainRange(3, canvas.height - 300, '#26333e')
                c.fillStyle = "#182028";
                c.fillRect(0, canvas.height -groundHeight,canvas.width,groundHeight )

                circles.forEach((i, index) => {
                    i.update()
                    if(i.radius=== 0 || i.radius < 0 ) circles.splice(index, 1) 
                })
                a.forEach((i, index) => {
                    i.update()
                    if(i.ttl=== 0 || i.ttl < 0 ) a.splice(index, 1)
                })
                ticker++
                if (ticker % 91=== 0) {


                    circles.push(
                        new Circle(Math.max(Math.random() * canvas.width - 20, 20), 0, 20, '#ffffff')
                    )
                }

            }



            init()
            animate()
        }
    }

    render() {
        return ( <div className = "fh" id = "p"        style = {
                {
                    height: "100vh",
                    position: 'fixed',
                    top: '0',
                    width: '100%'
                }
            } > <canvas> </canvas> </div>
        )
    }
}